code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
  monospace;
}

body, html {
  height: 100%;
  margin: 0;
}

#background {
  background-image: url(/portfolio/static/media/test4_pattern.343fa69d.png);
  background-repeat: repeat-y;
  background-size: 92.5vw;
  background-position: center;
  

}

#background2 {
  background-color: rgba(1, 10, 8, 0.75);
  /* background-color: rgba(0, 55, 47, 0.2); */
  

}
body, HTML {
    background-color: black;
    color: White;
}



.page {
   
}

.pageheading {
    margin-top: 20px;
    text-align: center;
    background-color: #295650;
    font-size: 26px;
    padding: 6px;
    margin-bottom: 20px;
}

#profile_pic {
    width: 280px;
    padding: 10px 0;
    margin: 20px 15px 20px 20px;
    
}




#about-me-info > p {
    margin: auto;
    width: 550;
    font-size: 20px;
    margin-left: 15px;
    
}

.logo_container > i {
    font-size: 8vw;
    color: #95C1B6

    
}

.logo_container {
    padding: 15px;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 5px;
}

#skill-area {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.horz-line {
    color:  #329a8c;
    background-color:  #329a8c;
    border-color:  #329a8c;
    height: 1px;
    margin:0 5px;
    padding:0;
    

}



@media (max-width: 950px) {
    .pageheading {
        font-size: 26px;
    }
    
    #profile_pic {
        width: 200px;
        padding: 10px 0;
        margin: 20px 15px 20px 20px;
        
    }
    
    
    
    
    #about-me-info > p {
        margin: auto;
        width: 550;
        font-size: 16px;
        margin-left: 15px;
        
    }
    
    .logo_container > i {
        font-size: 15vw;
        color: #95C1B6;
        padding: 0;
        margin: 0;
    
        
    }
    
    .logo_container {
        padding: 5px;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    
    #skill-area {
        display: flex;
        flex-direction: column;
        align-items: center
    }
    
    .horz-line {
        color:  #329a8c;
        background-color:  #329a8c;
        border-color:  #329a8c;
        height: 1px;
        margin:0 5px;
        padding:0;
        
    

  }
}

#navparent1 {
    display: flex;
    flex-direction: row;
    background-color: #333130;
    color: white;
    align-items: center;
    justify-content: space-around;
}

#navparent2 {
    display: flex;
    flex-direction: row;
}



.navelement  > a >  h4 {
    padding: 3px;
    margin: 0;
    border: 3px solid #295650;
    background-color: #295650;
    border-radius: 20px;
    font-size: 26px;
    
}
.navelement  > a {
   text-decoration: none;
   color: white;
}




#MyName > h4 {
    font-family: "Orbitron", 'Roboto', Garamond, sans-serif;
    margin: 0;
    padding: 0;
    /* margin-left: 10px; */
    margin: 0 0 0 10px;
    font-size: 26px;
    
    
}
#MyName > h5 {
    font-family: "Orbitron", 'Roboto', Garamond, sans-serif;
    margin: 0;
    padding: 0;
    margin-left: 10px;
    font-size: 18px;
    

}

@media (max-width: 768px) {
    
    #MyName > h5 {
        font-size: 12px;
    }

    #MyName > h4 {
        font-size: 18px;
    }

    .navelement  > h4 {
        font-size: 18px;
    }

  }
.carousel {
    overflow: hidden;

}

.inner {
    white-space: nowrap;
    transition: -webkit-transform 0.3s;
    transition: transform 0.3s;
    transition: transform 0.3s, -webkit-transform 0.3s;
}

.carousel-item {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    /* background-color: #333130; */
    color: white;
    
    


}

/* button styling */

.indicators {
    display: flex;
    justify-content: center;
}

.indicators > button {
    margin: 5px;
    border-radius: 30px;
    border: none;
    background-color: #2a7f75 ;
    color: white;
    font-size: 20px;
    padding: 7px;

}

.indicators > button.active {
    background-color: #30F3FF;
    color: #0d2321;
}


